/**
 * 
 * theme - sidebar theme ['dark', 'light'] 
 * layout -  ['sidemenu', 'topmenu', 'bothmenu'] 
 * sidebar - 
 * multiTab - 
 * contentWidth -   |  
 * fixedHeader -  Header : boolean
 * fixSiderbar -   boolean
 * fixedHeaderHidden -  Header : boolean
 * color - ,  localStorage
 * weak - 
 *
 */

export default {
  theme: "dark", // theme for nav menu
  layout: "sidemenu", // nav menu position: `sidemenu` or `topmenu`
  sidebar: true,
  multiTab: true,
  contentWidth: "Fluid", // layout of content: Fluid or Fixed, only works when layout is topmenu
  fixedHeader: false, // sticky header
  fixSiderbar: false, // sticky siderbar
  fixedHeaderHidden: false, //  auto hide header
  color: "#1890FF", // '#F5222D', // primary color of ant design
  weak: false,

  title: "SUMMARY",
  pwa: false,
  iconfontUrl: "",
  production:
    process.env.NODE_ENV === "production" &&
    process.env.VUE_APP_PREVIEW !== "true",
};
